body {
  background-image: linear-gradient(to right, var(--c-light), var(--c-dark)) !important;
}

svg {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.out-top {
  fill: var(--c-light);
  transform-origin: 13px 25px;
  animation: 60s linear infinite rotate;
}

.in-top {
  fill: var(--c-brand-light);
  transform-origin: 13px 25px;
  animation: 30s linear infinite rotate;
}

.out-bottom {
  fill: var(--c-brand-light);
  transform-origin: 84px 93px;
  animation: 75s linear infinite rotate;
}

.in-bottom {
  fill: var(--c-light);
  transform-origin: 84px 93px;
  animation: 45s linear infinite rotate;
}
